.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10vmin);
  color: white;
}

.generalFont {
  font-size: calc(5vmin);
  margin: 0px 100px;
  font-family: segoe print;
  stroke: white;
  text-shadow: 0 0 20px white;
}

.App-link {
  color: #61dafb;
}

.polaroid {
  background-clip: padding-box ;
  border-style: solid;
  border-width: contain;
  margin: 0px 20%;
  padding: 20px;
  object-fit: scale-down;
}

