body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10vmin);
  color: white;
}

.generalFont {
  font-size: calc(5vmin);
  margin: 0px 100px;
  font-family: segoe print;
  stroke: white;
  text-shadow: 0 0 20px white;
}

.App-link {
  color: #61dafb;
}

.polaroid {
  background-clip: padding-box ;
  border-style: solid;
  border-width: contain;
  margin: 0px 20%;
  padding: 20px;
  object-fit: scale-down;
}


#home_header {
    font-size: 3em;
}
a {
    text-decoration: none;
    font-size: 3vmin;
  }
  
  .navbar {
    background-color: #333333;
    display: flex;
    /* let's align the items so everything is nice and centered */
    align-items: center;
  }
  
  /* We want all links in the navbar to be #e7e7e7 */
  .navbar a {
    color: #e7e7e7;
  }
  
  .navbar ul {
   /* Let's reset the margin and padding of the list so it's is flush. */
    margin: 10px auto;
    /* Let's remove the bullet points */
    list-style-type: none;
    /* display the list flex and align the items centered */
    display: flex;
  }
  
  .navbar .brand {
    /*Let's make the anchor display block.
       This will ensure when we get the full width of the container.
    */
    
    color: #e7e7e7;
   /* Remove padding from top/bottom and add 24px padding right/left */
    padding: auto 24px;
    align-items: center;
  }
  
  .navbar ul li a {
    color: #e7e7e7;
    padding: 24px;
    margin: auto;
    /*Let's make the anchor display block.
       This will ensure when we hover, focus, or is active we get the full width and height 
        of the container.
    */
    display: block;
  }
  
  /* When a user hovers, focuses or if the link is active change the background */
  .navbar ul a:hover,
  .navbar ul a:focus,
  .navbar ul .active {
    background-color: #272727;
  }
